<template>
  <div id="login_bigBox">
    <div class="txal">
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="dslb demo-ruleForm loginFrom" style="width:300px;">
        <h1 style="font-size: 1.5rem;color: #999;font-weight: bold;padding: 1rem 0;">登陆</h1>
        <el-form-item >
          <el-input placeholder="账号"  v-model="ruleForm.userName"></el-input>
        </el-form-item>
        <el-form-item>
          <el-input  type="password" v-model="ruleForm.password" placeholder="密码"></el-input>
        </el-form-item>
        <img src="" style="width:100px;" alt="">
        <el-button type="primary" @click="submitForm('ruleForm')" style="width: 100%;">登陆</el-button>
        <div style="padding: 1rem 0 2rem 0;" class="clear">
          <div class="rt">
            <el-checkbox v-model="checked" style="color:#a0a0a0;">自动登录</el-checkbox>
          </div>
        </div>
      </el-form>
    </div>

  </div>
</template>
<script>
export default {
  data(){
    return {
      msg:'',
      ruleForm: {
       userName: '',
       password: '',
       code: '',
     },
     rules: {
      username: [{required: true, message: '请输入用户名', trigger: 'blur'}],
      password: [{required: true, message: '请输入密码', trigger: 'blur'}],
    },
      checked:false,
    }
  },
  mounted(){
    this.getCookie()
  },
  methods: {
    open(s){
      this.$message(s);
    },
    //点击登录调用方法
    submitForm(formName) {
      var name=this.ruleForm.userName;
      var pass=this.ruleForm.password;
      if(name==''){ this.open("请输入正确的用户名")
      return }else if(pass=='') { this.open("请输入正确的密码")
      return }
      var data={
        password:pass,
        name:name,
      };
      this.$api.login(data,res=>{
        // console.log(res)
        if(res.error_code==1000){
          this.open("无该店铺")
          return
        } else if (res.error_code==1001){
          this.open("店铺被冻结")
          return
        } else {
          sessionStorage.setItem("token", res.data[0].token);//存储
          //判断复选框是否被勾选
          if(this.checked==true){
            sessionStorage.setItem("key", this.$code.addcode(name,'key1'));//存储
            sessionStorage.setItem("pass", this.$code.addcode(pass,'key2'));//存储
          }
          this.$router.push('/')
        }
      })
    },
    getCookie:function () {
      var key=sessionStorage.getItem('key')
      var pass=sessionStorage.getItem('pass')
      if(key && pass){
        this.checked=true
        this.ruleForm.userName=this.$code.pullcode(key,'key1')
        this.ruleForm.password=this.$code.pullcode(pass,'key2')
      }

      // console.log(this.$code.pullcode(key,'key1'))
      // console.log(this.$code.pullcode(pass,'key2'))
      // if (document.cookie.length>0) {
      //   var arr=document.cookie.split('; ');//这里显示的格式需要切割一下自己可输出看下
      //   for(var i=0;i<arr.length;i++){
      //     var arr2=arr[i].split('=');//再次切割
      //     //判断查找相对应的值
      //     if(arr2[0]=='userName'){
      //       this.ruleForm.userName=this.$code.pullcode(arr2[1],'key1');//保存到保存数据的地方
      //     }else if(arr2[0]=='userPwd'){
      //       this.ruleForm.password=this.$code.pullcode(arr2[1],'key2');
      //     }
      //   }
      // }
    },
  },
}
</script>



