<template lang="">
    <div id='bigBox'>
      <!-- header -->
      <HeaderChaqnge @headerC='getheaderC' @chart='chart' :title='title'></HeaderChaqnge>
      <!-- content -->
      <div>
          <!-- one -->
          <div class="">
              <div class="fsb padd-25">
                  <h1>营业</h1>
                  <!-- <router-link to="/sale/view">更多</router-link> -->
              </div>
              <div class="fsb padd-30">
                  <div class="fsb bg-gray">
                      <div class=" mg-15">
                          <p class="padd-tb-10 blue">{{data.total_money | toUSD2}}</p>
                          <h3 class="padd-tb-10 ">营业收入（元）
                            <el-tooltip class="item" effect="dark" content="门店实际收入金额" placement="top">
                              <i class="el-icon-question"></i>
                            </el-tooltip>
                          </h3>
                      </div>
                      <i class="dslb h100pc" style="border: 1px solid #d2cbcb;height: 134px;"></i>
                      <div class="mg-15">
                          <div class="padd-tb-10">
                              <p class="">销售金额（元）
                                <el-tooltip class="item" effect="dark" content="全部商品销售收入总和" placement="top">
                                  <i class="el-icon-question"></i>
                                </el-tooltip>
                              </p>
                              <p class="">{{data.sales_complete}}</p>
                          </div>
                          <div class="padd-tb-10">
                              <p>退款金额（元）
                                <el-tooltip class="item" effect="dark" content="退款金额之和" placement="top">
                                  <i class="el-icon-question"></i>
                                </el-tooltip>
                              </p>
                              <p>{{data.refund_money}}</p>
                          </div>
                      </div>
                  </div>
                  <div class="fsb bg-gray">
                      <div class=" mg-15">
                          <p class="padd-tb-10 blue">{{data.order_total_no}}</p>
                          <h3 class="padd-tb-10">订单总数（笔）
                            <el-tooltip class="item" effect="dark" content="当前查询条件下门店实际订单笔数" placement="top">
                              <i class="el-icon-question"></i>
                            </el-tooltip>
                          </h3>
                      </div>
                      <i class="dslb h100pc" style="border: 1px solid #d2cbcb;height: 134px;"></i>
                      <div class="mg-15">
                          <div class="padd-tb-10">
                              <p class="">散客单数（笔）
                                <el-tooltip class="item" effect="dark" content="当前查询条件下门店散客订单总笔数" placement="top">
                                  <i class="el-icon-question"></i>
                                </el-tooltip>
                              </p>
                              <p class="">{{data.non_member_order_no}}</p>
                          </div>
                          <div class="padd-tb-10">
                              <p>会员单数（笔）
                                <el-tooltip class="item" effect="dark" content="当前查询条件下门店会员订单总笔数" placement="top">
                                  <i class="el-icon-question"></i>
                                </el-tooltip>
                              </p>
                              <p>{{data.member_order_no}}</p>
                          </div>
                      </div>
                  </div>
                  <div class="fsb bg-gray">
                      <div class=" mg-15">
                          <p class="padd-tb-10 blue">{{data.discount_money_total | toUSD2}}</p>
                          <h3 class="padd-tb-10">优惠金额（元）
                            <el-tooltip class="item" effect="dark" content="当前查询条件下所有优惠形式的优惠金额之和" placement="top">
                              <i class="el-icon-question"></i>
                            </el-tooltip>
                          </h3>
                      </div>
                      <i class="dslb h100pc" style="border: 1px solid #d2cbcb;height: 134px;"></i>
                      <div class=" mg-15">
                          <p class="padd-tb-10 blue">{{data.discount_order_no}}</p>
                          <h3 class="padd-tb-10">优惠单数（笔）
                            <el-tooltip class="item" effect="dark" content="当前查询条件下所有优惠形式的订单笔数之和（同一笔订单含有多个优惠，归结为一笔）" placement="top">
                              <i class="el-icon-question"></i>
                            </el-tooltip>
                          </h3>
                      </div>
                  </div>
              </div>
          </div>
          <!-- two -->
          <div class="">
              <div class=" padd-25">
                  <h1 class='txalS'>商品</h1>
                  <!-- <a href="#">更多</a> -->
              </div>
              <div class="fsa padd-30 mg-l-30 mg-r-30 bg-gray">
                  <div>
                      <h3 class="dslb">已上架商品数量（种）：</h3>
                      <span class='blue'>{{data.on_shelf_goods_no}}</span>
                  </div>
                  <div>
                      <h3 class="dslb">已下架商品数量（种）：</h3>
                      <span class='blue'>{{data.off_shelf_goods_no}}</span>
                  </div>
                  <div>
                      <h3 class="dslb">商品库存（件）：</h3>
                      <span class='blue'>{{data.inven_total}}</span>
                  </div>
              </div>
          </div>
          <!-- three -->
          <div class="">
              <div class="fsb padd-25">
                  <div class='txalS'>
                    <i class="dslb h100pc" style="border: 2px solid #d2cbcb;height: 20px;"></i>
                    <h1 class='dslb'>商品消费单数排名</h1>
                  </div>
                  <div>
                    <span :class='is_three?"blue":""' :style='' @click='isThree(0)' >销量</span>
                    <i class="dslb h100pc" style="border: 1px solid #d2cbcb;height: 20px;"></i>
                    <span :class='is_three?"":"blue"' :style='' @click='isThree(1)'>金额</span>
                  </div>
              </div>
              <div v-show='is_three' v-for='item in data.rank_by_money' class="fsb padd-30 mg-l-30 mg-r-30 mg-b-10 bg-gray" >
                <p>{{item.goods_name}}</p>
                <div class='fsb'>
                  <p class='padd-rl-12 dslb'>数量<span class='blue'>{{item.goods_total_no}}</span>份</p>
                  <p class='padd-rl-12 dslb'>占比<span class='blue'></span></p>
                  <p class='padd-rl-12 dslb'>环比<span class='blue'></span></p>
                </div>
              </div>
              <div v-show='!is_three' v-for='item in data.rank_by_no' class="fsb padd-30 mg-l-30 mg-r-30 mg-b-10 bg-gray" >
                <p>{{item.goods_name}}</p>
                <div class='fsb'>
                  <p class='padd-rl-12 dslb'>金额<span class='blue'>{{item.goods_total_price}}</span>元</p>
                  <p class='padd-rl-12 dslb'>占比<span class='blue'></span></p>
                  <p class='padd-rl-12 dslb'>环比<span class='blue'></span></p>
                </div>
              </div>
          </div>
          <!-- fore -->
          <div class="">
              <div class="fsb padd-25">
                  <div class='txalS'>
                    <h1 class='dslb'>会员</h1>
                  </div>
                  <div>
                    <router-link to="/member/allview">更多</router-link>
                  </div>
              </div>
              <div class="txalS padd-30 mg-l-30 mg-r-30 mg-b-5 bg-gray" >
                <div class='padd-l-24 mg-b-10'>
                  <i class="dslb h100pc circle" style="background:yellow;height: 20px;width:20px;"></i>
                  <p class='dslb'>新增会员<span class='blue'>{{data.new_member_no}}</span>人  消费 <span class='blue'>{{data.new_member_order_no}}</span>单  ，占比  <span class='blue'>{{(data.new_member_no/(data.new_member_no+data.old_member_no))*100 | toUSD}}</span>%</p>
                </div>
                <div class='padd-l-24 mg-b-10'>
                  <i class="dslb h100pc circle" style="background:red;height: 20px;width:20px;"></i>
                  <p class='dslb'>历史会员<span class='blue'>{{data.old_member_no}}</span>人  消费 <span class='blue'>{{data.old_member_order_no}}</span>单  ，占比  <span class='blue'>{{data.old_member_no/(data.new_member_no+data.old_member_no)*100 | toUSD}}</span>%</p>
                </div>
                <div class='padd-l-24 mg-b-10'>
                  <i class="dslb h100pc circle" style="background:green;height: 20px;width:20px;"></i>
                  <p class='dslb'>全部会员<span class='blue'>{{data.new_member_no+data.old_member_no}}</span>人  消费 <span class='blue'>{{data.new_member_order_no+data.old_member_order_no}}</span>单  ，占比  <span class='blue'>100</span>%</p>
                </div>
              </div>
          </div>
          <!-- five -->
          <div class="">
              <div class=" padd-25">
                  <div class='txalS'>
                    <h1 class='dslb'>消息提示</h1>
                  </div>
              </div>
              <div class="txalS padd-30 mg-l-30 mg-r-30 mg-b-5 bg-gray" >
                <div class=' padd-l-24 mg-b-30'>
                  <div class='dslb'>
                    <i class="dslb h100pc circle" style="background:gray;height: 20px;width:20px;"></i>
                    <h3 class='dslb'>商品库存预警（种）</h3>
                  </div>
                  <router-link to="/shopping/store/del">{{data.inven_warning}}</router-link>
                </div>
                <div class=' padd-l-24 mg-b-30'>
                  <div class='dslb'>
                    <i class="dslb h100pc circle" style="background:gray;height: 20px;width:20px;"></i>
                    <h3 class='dslb'>退货操作</h3>
                  </div>
                  <span class='mg-l-30 blue'>{{data.refund_order_no}}(单)</span>
                  <span class='mg-l-30 blue'>{{data.refund_money | toUSD}}(元)</span>
                </div>
              </div>
          </div>
      </div>
    </div>

</template>

<script>
import HeaderChaqnge from '../../components/headerChange.vue'
export default {
  components:{
    HeaderChaqnge,
  },
  name:'index',
  data(){
    return {
      title:'整体概况',
      is_today:true,
      is_yesterday:false,
      is_week:false,
      is_month:false,
      value_date:[],
      data:{},
      is_three:true,
    }
  },
  created() {
    this.getdata()
  },
  mounted() {
  },
  methods: {
    getheaderC(data){
      // console.log(data)
      switch (data) {
        case 0:
          this.is_today=true;
          this.is_yesterday=false;
          this.is_week=false;
          this.is_month=false;
          this.value_date=[]
          this.getdata()
          break;
        case 1:
          this.is_yesterday=true;
          this.is_today=false;
          this.is_week=false;
          this.is_month=false;
          this.value_date=[]
          this.getdata()
          break;
        case 2:
          this.is_week=true;
          this.is_yesterday=false;
          this.is_today=false;
          this.is_month=false;
          this.value_date=[]
          this.getdata()
          break;
        case 3:
          this.is_month=true;
          this.is_week=false;
          this.is_yesterday=false;
          this.is_today=false;
          this.value_date=[]
          this.getdata()
          break;
        case 4:
          this.is_today=false;
          this.is_yesterday=false;
          this.is_week=false;
          this.is_month=false;
          this.value_date=[]
          break;
        default:
          break;
      }
    },
    isThree(type){
      this.is_three=!this.is_three
    },
    chart(data){
      // console.log(data)
      this.value_date=data.value1
      this.getdata()
    },
    getdata(){
      let data={
        start_date:this.value_date.length>0?this.value_date[0]:'',
        end_date:this.value_date.length>0?this.value_date[1]:'',
        is_today:this.is_today?1:2,
        is_yesterday:this.is_yesterday?1:2,
        is_week:this.is_week?1:2,
        is_month:this.is_month?1:2,
      }
      console.log(data)
      this.$api.home(data,res=>{
        console.log(res)
        if(res.data.length==0){return false}
        this.data=res.data[0].list
      })
    }
  },
}
</script>

<style lang="less" scoped>
  #bigBox{
    text-align: center;
    #box{
      color: #583495;
      .one{
        display: block;
        font-size: 100px;
      }
      .two{
        transform: rotate(30deg);
        display: inline-block;
      }
    }
  }
</style>




