<template>
  <div id='header_box'>
    <div class="flex aic mg-20 jc-end">
      <img src="../../static/header_img/header_1.png" alt="">
      <span class="dslb mg-l-10">星星便利店</span>
      <img class="dslb mg-l-10" @click="clear" src="../../static/header_img/header_2.png" alt="">
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {

    }
  },
  methods: {
    clear(){
      var data={}
      this.$api.logout(data,res=>{
        // console.log(res)
        sessionStorage.removeItem('token')
        this.$router.push('/login')
      })
    }
  },
}
</script>



