<template>
  <div id="nav_box" class="rel">
    <el-tree  :data="data"
     accordion
     :default-expanded-keys='curr_arr'
     node-key="router"
     highlight-current
     :props="defaultProps"
     @node-click="handleNodeClick"
     class=""
     ref="tree"
     ></el-tree>
  </div>
</template>
<script>
export default {
  data() {
    return {
      data: [
        {
          label: '概览',
          index:'0',
          router:'/',
        },
        {
          label: '销售',
          index:'1',
          children: [
            {
              label: '营业概况',
              index:'1_0',
              router:'/sale/view',
            },
            {
              label: '数据分析',
              // index:'1_1',
              children: [
                {
                  label: '营业趋势分析',
                  index:'1_1_0',
                  router:'/sale/data/trend',
                },
                {
                  label: '营业占比分析',
                  index:'1_1_1',
                  router:'/sale/data/proportion',
                },
                {
                  label: '商品销售统计',
                  index:'1_1_2',
                  router:'/sale/data/sale',
                },
                {
                  label: '笔单价分析',
                  index:'1_1_3',
                  router:'/sale/data/one',
                },
              ]
            },
            {
              label: '订单中心',
              // index:'1_2',
              children: [
                {
                  label: '订单数据',
                  index:'1_2_0',
                  router:'/sale/order/data',
                },
              ]
            },
            {
              label: '交班记录',
              // index:'1_3',
              children: [
                {
                  label: '日结记录',
                  index:'1_3_0',
                  router:'/sale/record/day'
                },
                {
                  label: '交班记录',
                  index:'1_3_1',
                  router:'/sale/record/class'
                },
              ]
            },
          ],
        },
        {
          label: '商品',
          children: [
            {
              label: '商品概览',
              index:'2_0',
              router:'/shopping/overview'
            },
            {
              label: '商品中心',
              children: [
                {
                  label: '商品资料',
                  index:'2_1_0',
                  router:'/shopping/center/data'
                },
                {
                  label: '商品分类',
                  index:'2_1_1',
                  router:'/shopping/center/class'
                },
                {
                  label: '商品设置',
                  index:'2_1_2',
                  router:'/shopping/store/change'
                },
              ]
            },
            {
              label: '库存',
              children: [
                {
                  label: '库存查询',
                  index:'2_2_0',
                  router:'/shopping/store/chart'
                },
                {
                  label: '库存预警',
                  index:'2_2_1',
                  router:'/shopping/store/del'
                },
                {
                  label: '变动明细',
                  index:'2_2_2',
                  router:'/shopping/store/change'
                },
                {
                  label: '盘点历史',
                  index:'2_2_3',
                  router:'/shopping/store/history'
                },
              ]
            },
          ],
        },
        {
          label: '员工',
          children: [
            {
              label: '员工中心',
              children:[
                {
                  label: '员工管理',
                  index:'3_0_0',
                  router:'/staff/staffCen/create'
                },
                {
                  label: '收银员业绩',
                  index:'3_0_1',
                  router:'/staff/staffCen/succ'
                },
              ]
            },
          ],
        },
        {
          label: '会员',
          children: [
            {
              label: '会员概览',
              index:'4_0',
              router:'/member/allview'
            },
            {
              label: '会员中心',
              children:[
                {
                  label:'会员管理',
                  index:'4_1_0',
                  router:'/member/center/create'
                }
              ]
            },
            {
              label: '统计报表',
              children:[
                {
                  label:'会员消费信息分析',
                  index:'4_2_0',
                  router:'/member/table/massge'
                },
                {
                  label:'客单价分析',
                  index:'4_2_1',
                  router:'/member/table/once'
                },
                {
                  label:'积分分布',
                  index:'4_2_2',
                  router:'/member/table/scor'
                },
              ]
            },
          ],
        },
        {
          label: '数据',
          index:'5',
          router:'/data'
        },
        {
          label: '设置',
          children: [
            {
              label: '店铺信息管理',
              index:'6_0',
              router:'/create/data'
            },
            {
              label: '系统设置',
              index:'6_1',
              router:'/create/sysCra'
            },
            {
              label: '操作日志',
              index:'6_2',
              router:'/create/useDate'
            }
          ],
        }
      ],
      defaultProps: {
        children: 'children',
        label: 'label'
      },
      curr_key:'',
      curr_arr:[],
    }
  },
  watch: {
    $route:function (n,o) {
      // console.log(n)
      this.$refs.tree.setCurrentKey(n.path)
      this.curr_arr=[n.path]
    }
  },
  methods: {
    handleNodeClick(data) {
      // console.log(this.$refs.tree.getCurrentKey())
      switch (data.index) {
        case '0':
          this.$router.push('/')
          break;
        case '1':
          // this.$router.push('/sale/view_0')
          break;
        case '1_0':
          // this.$router.push('/sale/view')

          break;
        case '1_1_0':
          this.$router.push('/sale/data/trend')
          break;
        case '1_1_1':
          this.$router.push('/sale/data/proportion')
          break;
        case '1_1_2':
          this.$router.push('/sale/data/sale')
          break;
        case '1_1_3':
          this.$router.push('/sale/data/one')
          break;
        case '1_2_0':
          this.$router.push('/sale/order/data')
          break;
        case '1_3_0':
          // this.$router.push('/sale/record/day')
          break;
        case '1_3_1':
          this.$router.push('/sale/record/class')
          break;
        case '2_0':
          this.$router.push('/shopping/overview')
          break;
        case '2_1_0':
          this.$router.push('/shopping/center/data')
          break;
        case '2_1_1':
          // this.$router.push('/shopping/center/class')
          break;
        case '2_1_2':
          this.$router.push('/shopping/center/create')
          break;
        case '2_2_0':
          this.$router.push('/shopping/store/chart')
          break;
        case '2_2_1':
          // this.$router.push('/shopping/store/del')
          break;
        case '2_2_2':
          // this.$router.push('/shopping/store/change')
          break;
        case '2_2_3':
          // this.$router.push('/shopping/store/history')
          break;
        case '3_0_0':
          this.$router.push('/staff/staffCen/create')
          break;
        case '3_0_1':
          this.$router.push('/staff/staffCen/succ')
          break;
        case '4_0':
          this.$router.push('/member/allview')
          break;
        case '4_1_0':
          this.$router.push('/member/center/create')
          break;
        case '4_2_0':
          this.$router.push('/member/table/massge')
          break;
        case '4_2_1':
          this.$router.push('/member/table/once')
          break;
        case '4_2_2':
          this.$router.push('/member/table/scor')
          break;
        case '5':
          this.$router.push('/data')
          break;
        case '6_0':
          this.$router.push('/create/data')
          break;
        case '6_1':
          this.$router.push('/create/sysCra')
          break;
        case '6_2':
          // this.$router.push('/create/useDate')
          break;

        default:
          break;
      }
    }
  }
}
</script>



<style lang="less">
.el-tree--highlight-current .el-tree-node.is-current>.el-tree-node__content {
  background: #a8a8dc;
}
.el-tree{
  height: 100vh;
  background: #f8f8f8;
  position: fixed;
  top: 0px;
  .el-tree-node{
    .el-tree-node__content{
      height: 50px;
      .el-tree-node__label{
        padding-right: 22px;
      }
    }

  }
  .el-tree-node:focus>.el-tree-node__content{
    background-color: #a8a8dc;
  }
}

</style>






